p.hanging-indent {
    text-indent: -2em;
    padding-left: 2em;
}


body.context-combatant .combatant-context-hide {
    display: none;
}

body:not(.context-combatant) .not-combatant-context-hide {
    display: none;
}

body.context-combatant .combatant-context-match-hide.context-combatant-match {
    display: none;
}

body.context-combatant .combatant-context-unmatch-hide:not(.context-combatant-match) {
    display: none;
}


body.context-apply .apply-context-hide {
    display: none;
}

body:not(.context-apply) .not-apply-context-hide {
    display: none;
}


body.context-remove .remove-context-hide {
    display: none;
}

body:not(.context-remove) .not-remove-context-hide {
    display: none;
}


body.context-display-only .badge {
    font-size: 1.5em;
    display: inline-block;
    margin-bottom: 0.25rem;
}

body.context-display-only .combatant-card-container {
    max-width: none;
    padding-right: 100px;
    padding-left: 100px;
}

body.context-display-only .display-only-context-hide {
    display: none;
}

body:not(.context-display-only) .not-display-only-context-hide {
    display: none;
}

body:not(.context-display-only) .combatant-card-body {
    padding-right: 0.5rem;
    padding-left: 0.5rem;
}


@media (max-width: 991.98px) {
    .navbar .dropdown-menu {
        border: none;
    }

    #navbar-login-logout {
        max-width: 400px;
    }
}

@media (min-width: 992px) {
    .dm-screen-tab {
        column-count: 2;
    }
}
@media (min-width: 1200px) {
    .dm-screen-tab {
        column-count: 3;
    }
}
@media (min-width: 1500px) {
    .dm-screen-tab {
        column-count: 4;
    }
}
@media (min-width: 1800px) {
    .dm-screen-tab {
        column-count: 5;
    }
}
.dm-screen-tab p {
    white-space: pre-line;
    /*-webkit-column-break-inside:avoid;*/
    /*-moz-column-break-inside:avoid;*/
    /*-o-column-break-inside:avoid;*/
    /*-ms-column-break-inside:avoid;*/
    column-break-inside:avoid;
}